<template>
  <div class="home">
    <scroll class="home-content" :data="dissList">
      <div>
        <div class="slider-wrapper" v-if="Slider.length">
          <slider>
            <div v-for="item in Slider">
              <a :href="item.url">
                <img :src="item.pic[0]" :alt="item.title">
                <span class="slider-title" v-html="item.title"></span>
              </a>
            </div>
          </slider>
        </div>
        <div class="home-list">
          <ul>
            <li v-for="item in dissList" class="item">
              <div class="box" v-if="!item.modelmode">
                <div class="icon" v-if="item.imgsrc">
                  <img v-lazy="item.imgsrc" width="100%"/>
                </div>
                <div class="text">
                  <div class="title">
                    <h2 class="title-name" v-html="item.title"></h2>
                  </div>
                  <div class="desc">
                    <span class="pubdate">{{item.ptime}}</span>
                    <span class="Comment-number">{{item.commentCount}}</span>
                    <i class="iconfont icon-code"></i>
                  </div>
                </div>
              </div>

              <div class="box2" v-if="item.modelmode">
                <div class="text2">
                  <h2 class="title-name" v-html="item.title"></h2>
                </div>
                <div class="icon2" v-if="item.imgsrc">
                  <img v-lazy="item.imgsrc" width="32%" height="82.78" class="icon2-img"/>
                  <img v-lazy="item.imgextra[0].imgsrc" width="32%" class="icon2-img"/>
                  <img v-lazy="item.imgextra[1].imgsrc" width="32%"/>
                </div>
                <div class="desc2">
                  <span class="pubdate">{{item.ptime}}</span>
                  <span class="Comment-number">{{item.commentCount}}</span>
                  <i class="iconfont icon-code"></i>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!dissList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
  import axios from "axios"
  import Slider from "base/slider/slider"
  import Scroll from "base/scroll/scroll"
  import Loading from "base/loading/loading"
  export default {
    data(){
      return {
        dissList:[],
        Slider:[]
      }
    },
    components:{
      Slider,
      Scroll,
      Loading
    },
    created(){
      this._getSlider();

    },
    methods:{
      _getSlider(){
        axios.get('http://120.78.94.107:8080/api/getSlider').then((res)=>{
          this.Slider=res.data.swiper
//          console.log(this.Slider);
          this._getDissList();
        })
      },
      _getDissList(){
        axios.get('http://120.78.94.107:8080/api/getDissList').then((res)=>{
          this.dissList=res.data.BBM54PGAwangning;
//          console.log(res.data)
//          console.log(this.dissList);

        })
      }
    }

  }
</script>


<style scoped lang="stylus">
  .home
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .home-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position relative
        width: 100%
        overflow: hidden
        .slider-title
          position absolute
          left 10px
          bottom 10px
          color: #fff
      .home-list
        .item
          /*display: flex*/
          box-sizing: border-box
          align-items: center
          padding: 10px 0px
          margin: 0px 15px
          border-bottom: 1px solid #e5e5e5
          .box
            display: flex
            box-sizing: border-box
            align-items: center
          .icon
            flex: 0 0 93px
            width: 27%
            margin-right: 10px
          .text
            display: flex
            flex-direction: column
            flex: 1
            overflow: hidden
            font-size: $font-size-medium
            .title
              line-height:21px
              font-size: 18px
              color: #404040
              font-weight:normal
              margin-bottom 10px
            .desc
              span
                display inline-block
                font-size 14px
                color: #888
                padding-left 5px
              .Comment-number
                float right
              .icon-code
                float right
        .box2
          box-sizing: border-box
          align-items: center
          .text2
            line-height:21px
            font-size: 18px
            color: #404040
            font-weight:normal
            margin-bottom 10px
          .icon2
            overflow hidden
            margin 11px 0px
          .icon2-img
            float left
            margin-right 2%
          .desc2
            span
              display inline-block
              font-size 14px
              color: #888
              padding-left 5px
            .Comment-number
              float right
            .icon-code
              float right

  .loading-container
    position: absolute
    width: 100%
    top: 50%
    transform: translateY(-50%)
</style>



