<template>
  <div class="news-header">
    <div class="news-logo"></div>
    <router-link class="mine"  to="/user">
      <i class="iconfont icon-account"></i>
      <i class="iconfont icon-xiazai"></i>
      <i class="iconfont icon-email"></i>
    </router-link>
  </div>
</template>

<script>
    export default {}
</script>

<style scoped lang="stylus">
  @import "~common/stylus/variable";
  @import "~common/stylus/mixin"
    .news-header
      position relative
      width: 100%
      height: 44px
      text-align center
      background-color $color-text
    .news-logo
      display: inline-block
      vertical-align: top
      margin-top: 10px
      width: 120px
      height: 25px
      bg-image('logo')
      background-repeat no-repeat
      background-size: 120px 25px
    .mine i
      position absolute
      color: #999
    .icon-account
      left: 12px
      top: 11px
      font-size: 25px
    .icon-xiazai
      right: 45px
      top: 10px
      font-size: 25px
    .icon-email
      right: 12px
      top: 10px
      font-size: 25px
</style>



